<template>
<div class="Bg-Lines">
	<div class="Bg-Line Bg-Line-1">
		<div class="Bg-Line-Animation"></div>
	</div>
	<div class="Bg-Line Bg-Line-2"></div>
	<div class="Bg-Line Bg-Line-3"></div>
	<div class="Bg-Line Bg-Line-4">
		<div class="Bg-Line-Animation"></div>
	</div>
	<div class="Bg-Line Bg-Line-5"></div>
	<div class="Bg-Line Bg-Line-6">
		<div class="Bg-Line-Animation"></div>
	</div>
	<div class="Bg-Line Bg-Line-7"></div>
</div>
</template>

<script>
export default {
	name: 'BgLines',
	props: {
		ButtonText: {
			type: String,
			default () {
				return ""
			}
		},
	},
	components: {},
	data() {
		return {}
	},
	created() {

	},
	watch: {

	},
	computed: {

	},
	methods: {

	},

	mounted() {

	}
}
</script>

<style lang="scss">@import "../style/style.scss";
.Bg-Line {
    position: absolute;
    top: 0;
    width: 1px;
    height: 100%;
    z-index: 1;
    background-color: color(border);

}
.Bg-Line {
    .Bg-Line-Animation {
        position: relative;
        overflow: hidden;
        width: 3px;
        left: -1px;
        height: 140px;
        &::before {
            position: absolute;
            content: "";
            transform: translateY(-100%);
            width: 100%;
            height: 50px;
            background-color: color(prime);
            animation-name: lineanimation;
            animation-duration: 3s;
            animation-iteration-count: 1;
        }
    }
}
.Bg-Line-1 {
    .Bg-Line-Animation {
        top: 20%;
        &::before {
            height: 80px;
        }
    }
}
.Bg-Line-4 {
    .Bg-Line-Animation {
        top: 85%;
        &::before {
            animation-delay: 2s;
        }
    }
}
.Bg-Line-6 {
    .Bg-Line-Animation {
        top: 50%;
        &::before {
            height: 120px;
            animation-delay: 1s;
        }
    }
}
@keyframes lineanimation {
    100% {
        transform: translateY(400%);
    }
}
$elements: 8;
@for $i from 1 to $elements {
    .Bg-Line:nth-child(#{$i}) {
        left: calc(#{$i} * 12.5vw - 1px);
    }
}
</style>



