<template>
<section class="Intro">
	<BgLines></BgLines>
</section>
</template>

<script>
import BgLines from '../components/BgLines.vue';

export default {
	name: 'Intro',
	props: {
		ButtonText: {
			type: String,
			default () {
				return ""
			}
		},
	},
	components: {
		BgLines
	},
	data() {
		return {}
	},
	created() {

	},
	watch: {

	},
	computed: {

	},
	methods: {

	},

	mounted() {

	}
}
</script>

<style lang="scss">@import "../style/style.scss";
$elements: 15;
.Intro {
    width: 100%;
    height: 100vh;
    background-color: white;
    z-index: zIndex(intro);
    position: absolute;
    animation-name: fadeOut;
    animation-fill-mode: forwards;
    animation-delay: 1.5s;
    animation-duration: 0.5s;
    pointer-events: none;
    &::after {
        position: absolute;
        content: "";
        top: 0;
        width: 100%;
        height: 100vh;
        background-color: color(bg-start);
        transform: translateX(-100vw);
        animation: fadeIn;
        animation-fill-mode: forwards;
        animation-duration: 0.8s;
        animation-timing-function: ease-in-out;
    }

    &::before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        height: 5px;
        background: color(prime);
        z-index: zIndex(default)+1;
    }
    .Bg-Line {
        animation-name: show;
        animation-fill-mode: forwards;
        animation-duration: 0.5s;
        animation-timing-function: ease-in-out;
        transform: translateY(-100vh);
        @for $i from 0 through $elements {
            &:nth-child(#{$i}) {
                animation-delay: calc(#{$i} * 0.125s);
            }
        }
        @media (max-width: 800px) {
            background-color: rgba(#5121E6,0.07);
        }
        @media (max-width: 500px) {
            background-color: rgba(#5121E6,0.07);
        }
    }
    @keyframes show {
        100% {
            opacity: 1;
            transform: translateY(0);
        }
    }
    @keyframes fadeIn {
        100% {
            transform: translateX(0);
        }
    }
    @keyframes fadeOut {
        100% {
            opacity: 0;
        }
    }

}
</style>



